const lengthIn = (min: number = 0, max: number = Infinity) => (str: string) => {
	if(str == null)
		return null;

	let resultString = '   ';

	if(min)
		resultString += `  ${min}`;

	if(isFinite(max))
		resultString += `  ${max}`;

	return str && str.length >= min && str.length <= max ? null : resultString;
};

export default lengthIn;
